@import 'variables.less';

@primary-color: @green-2;
@height-base: 40px;
@btn-font-weight: bold;
@border-radius-base: @radius-sm;
@border-width-base: 2px;
@btn-default-bg: @black;
@btn-default-color: @white;
@btn-danger-bg: @grey-2;
@btn-danger-color: @white;
@btn-default-ghost-color: @black;
@btn-default-ghost-bg: @green-5;
@layout-header-height: 87px;
@layout-sider-background: @white;
@menu-item-active-border-width: 0px;
// @tabs-active-color: @black;
@tabs-highlight-color: @black;
@tabs-ink-bar-color: @black;
@tabs-hover-color: @black;
@btn-shadow: none;
@btn-primary-shadow: none;
@btn-text-shadow: none;

.ant-btn {
  .shadow-sm();
}

.ant-btn-background-ghost {
  color: @black!important;
  box-shadow: none;
}
.ant-btn-primary {
  box-shadow: none;
}
.ant-btn-round {
  text-shadow: none;
  display: flex;
  align-items: center;
  padding: 8px 10px !important;
  border: none;
  background-color: @grey-5!important;
  height: 32px;
}
.ant-btn:hover,
.ant-btn:focus {
  color: @white;
}
