.font-create(@font, @weight: normal, @size, @height: normal, @spacing:0) {
  font-family: @font;
  font-weight: @weight;
  font-size: @size;
  line-height: @height;
  letter-spacing: @spacing;
}
.shadow-create(@x: 0, @y: 0, @blur: 0px, @spread: 0px, @color: #000) {
  -webkit-box-shadow: @arguments;
  -moz-box-shadow: @arguments;
  box-shadow: @arguments;
}
