@import '../../assets/less/variables.less';

.container {
  width: 900px;
  margin: 0 auto;
}

.no-margin {
  margin: 0;
}

.header-text {
  .header();
  margin: 0px;
}

.spacing {
  .wrapper {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
    div {
      background-color: #ff7a7a;
      display: inline-block;
    }
    span {
      .button-copy();
      text-transform: none;
      margin-left: 20px;
    }
  }
}
.colors {
  .container {
    display: flex;
    .wrapper {
      margin-right: 4px;
    }
  }
  .green-1 {
    background-color: @green-1;
  }
  .green-2 {
    background-color: @green-2;
  }
  .green-3 {
    background-color: @green-3;
  }
  .green-4 {
    background-color: @green-4;
  }
  .green-5 {
    background-color: @green-5;
  }
  .green-6 {
    background-color: @green-6;
  }
  .grey-1 {
    background-color: @grey-1;
  }
  .grey-2 {
    background-color: @grey-2;
  }
  .grey-3 {
    background-color: @grey-3;
  }
  .grey-4 {
    background-color: @grey-4;
  }
  .grey-5 {
    background-color: @grey-5;
  }
  .white {
    background-color: @white;
  }
  .black {
    background-color: @black;
  }
}

.shadows {
  .container {
    display: flex;
  }
  .shadow-lg {
    .shadow-lg();
  }
  .shadow-sm {
    .shadow-sm();
    margin-left: 20px;
  }
}

.radiuses {
  .container {
    display: flex;
    .wrapper {
      margin-right: 20px;
      text-align: center;
    }
  }
}

.fonts {
  .header {
    .header();
  }
  .title-1 {
    .title-1();
  }
  .title-2 {
    .title-2();
  }
  .title-3 {
    .title-3();
  }
  .title-4 {
    .title-4();
  }
  .copy-1 {
    .copy-1();
  }
  .copy-2 {
    .copy-2();
  }
  .button-copy {
    .button-copy();
    text-transform: uppercase;
  }
}

.icons {
  .ant-table-row {
    background-color: #ebebeb;
  }
  .ant-table-tbody > tr.ant-table-row:hover > td {
    background-color: #ebebeb;
  }
}

.dashboard {
  .ant-layout-sider {
    border-right: 1px solid #979797;
    .ant-menu-item {
      .copy-1();
      font-size: 14px;
      display: flex;
      align-items: center;
      padding: 15px !important;
      height: 52px;
      margin: 0px;
      border-bottom: 1px solid #cacaca;
      &:last-child {
        border: none;
      }
      span {
        margin-left: 10px;
      }
    }
    .ant-menu-item-selected {
      background: rgba(0, 149, 59, 0.05) !important;
      color: @black;
      .copy-1();
      font-size: 14px;
      font-weight: 800;
    }
  }
  .ant-tabs-tab {
    .copy-1();
    padding-left: 19px;
    padding-right: 19px;
  }
  .ant-tabs-tab-active {
    .copy-1();
    // .title-3();
  }
  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    font-weight: 800;
  }
  .nav-header {
    background-color: white;
  }
}
