@import './utils.less';

// Colors

@green-1: #0e7f3a;
@green-2: #00953b;
@green-3: #33aa62;
@green-4: #b3dfc4;
@green-5: #ecf7f0;
@green-6: #f5fbf7;

@grey-1: #636363;
@grey-2: #98989a;
@grey-3: #cbcbcc;
@grey-4: #e0e0e1;
@grey-5: #f5f5f5;

@white: #ffffff;
@black: #000000;

// Spacing units
@space-xxl: 64px;
@space-xl: 48px;
@space-lg: 32px; // containers
@space-md: 24px; // small containers and buttons
@space-sm: 16px; // Form controls and items
@space-xs: 12px; // small items
@space-xss: 8px; // more small

// Radiuses
@radius-sm: 4px;
@radius-md: 8px;
@radius-lg: 16px;
@radius-badge: 12px;
@radius-icon: 70px;

// Shadows
.shadow-lg() {
  .shadow-create(0px, 4px, 6px, @color: rgba(0, 0, 0, 0.15));
}
.shadow-sm() {
  .shadow-create(0px, 4px, 8px, 2px, rgba(0, 0, 0, 0.05));
}

// Fonts
.header() {
  .font-create(@font: 'dm_serif_displayregular', @size: 45px);
}
.title-1() {
  .font-create(@font: 'dm_sansbold', @size: 30px, @weight: bold, @height: 38px);
}
.title-2() {
  .font-create(@font: 'dm_sansbold', @size: 22px, @weight: bold, @height: 30px);
}
.title-3() {
  .font-create(@font: 'dm_sansbold', @size: 16px, @weight: bold, @height: 24px);
}
.title-4() {
  .font-create(@font: 'dm_sansbold', @size: 14px, @weight: bold, @height: 22px);
}
.copy-1() {
  .font-create(@font: 'dm_sansregular', @size: 16px, @height: 24px);
}
.copy-2() {
  .font-create(@font: 'dm_sansregular', @size: 16px, @height: 24px);
  color: #626262;
}
.button-copy() {
  .font-create(@font: 'dm_sansbold', @size: 12px, @height: 14px, @spacing: 0.6px);
  text-transform: uppercase;
}
